import MissionHeader from './missionComponents/header'
import MissionDetail from './missionComponents/detail'
import axios from 'axios'

export default {
  name: 'mission',
  components: {
    MissionHeader,
    MissionDetail
  },
  data () {
    return {
      mission: Object,
      name: this.name
    }
  },
  methods: {
    getMission () {
      axios.get(this.url + '/mission/id/' + this.$route.params.miId)
        .then(this.getMissionSucc)
    },
    getMissionSucc (res) {
      if (res) {
        const data = res.data
        this.mission = data
        this.name = data.name
      }
    }
  },
  mounted () {
    this.getMission()
  }
}



